<template>
  <div class="PARTLY">
    <div class="headPart">
      <img :src="container[0][2]" alt="">
    </div>
      <el-table :data="container" stripe style="width:80%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="" label="歌曲" width="500">
          <template v-slot="scoped">
            <div class = 'item'>
              <a href="javascript:"><i class='iconfont icon-bofang' @click="playOrPause($event,scoped.row[1])"></i></a>
              <div>{{ scoped.row[0] }}</div>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="" label="歌手" width="200">
          <template v-slot="scoped">
            <div>{{scoped.row[3]}}</div>
          </template>
        </el-table-column>
      </el-table>
  </div>

</template>

<script>
import {defineComponent, inject, onMounted, reactive, ref} from "vue";
import { useRouter } from 'vue-router'
export default defineComponent({
  name: "subHome",
  setup(){
    const router = useRouter()
    let  v= router.currentRoute.value.params
    let container=[]
    let ar = v['authorName']
    let audio = new Audio()
    let playOrPause = function(e,m){
      e.target.classList.value =
          e.target.classList.value === "iconfont icon-zanting"?
              "iconfont icon-bofang":
              "iconfont icon-zanting"
      if(!audio.paused){
        audio.pause()
      }
      if(m!==audio.src) {
        audio.src = m;
        audio.play()
      }
    }
    v = v['content']
    v.forEach((item,index)=>{
      let array = []
      item.split(',').forEach((it,index)=>{
        array.push(it)
        if((index+1)%3 === 0){
          array.push(ar[index])
          container.push(array)
          array = []
        }
      })
    })
    return {
      container,
      playOrPause,
    }
  }
 })
</script >

<style scoped>
* {
  color: black;
  text-decoration: none;
  text-align: left;
  /*line-height: 1;*/
}

li {
  list-style: none;
}

.iconfont {
  font-size: 20px;
}

.headPart {
  position: relative;
  z-index: 999;
  left: 10%;
  /*top: 15%;*/
  top: -10%;
  width: 100%;
}

.headPart img {
  width: 40%;
  height: 30%;
  margin-bottom: 10%;
}

.el-table {
  position: relative;
  top: 30%;
  margin:0 auto;
  /*left: 20%;*/
}
.PARTLY{
  position: absolute;
  top: 20%;
  width: 50%;
  left: 20%;
  border:solid 2px #f8f8f8;
  border-radius: 1%;
  background-color: white;
}
.item{
  display: flex;
}
.item div{
  margin-left: 20px;
}
</style>
