<template>
  <div class = 'footerAudio'>
    <div class = 'main-audio'>
      <a href="javascript:" tilte="上一首"><i class="iconfont icon-shangyishoushangyige"></i></a>
      <a href="javascript:" title="播放/暂停"><i class="iconfont icon-zanting" @click="playOrPause"></i></a>
      <a href="javascript:" title="下一首"><i class="iconfont icon-xiayigexiayishou"></i></a>
    </div>
  </div>
</template>

<script>
import {defineComponent} from "vue";

export default defineComponent({
  name: "footerAudio",
  setup(){
    let playOrPause = function(e){
      e.target.classList.value =
          e.target.classList.value === "iconfont icon-zanting"?
          "iconfont icon-bofang":
          "iconfont icon-zanting"
    }
    return {
      playOrPause
    }
  }
})
</script>

<style scoped>
*{
  text-decoration: none;
  color: black;
}
  .footerAudio{
    position: fixed;
    width: 100%;
