<template>
  <div>
    <lunbo></lunbo>
    <div class = 'home'>
      <router-link :to="{name:'subHome',params:{'content':content,authorName:authorName}}" v-for="(url,index) in coverImgUrl" :key="index">
          <img :src="url[0]" :alt="index">
          <span>{{url[1]}}</span>
      </router-link>
    </div>
  </div>
</template>

<script>
import {defineComponent, getCurrentInstance, onBeforeMount, provide, ref} from "vue";
import footerAudio from "../footerAudio";
import subHome from "../subHome";
import lunbo from '../lunbo'
import {useRouter} from "vue-router";
export default defineComponent({
  name: "Home",
  components:{lunbo,footerAudio},
  setup() {
    const {proxy} = getCurrentInstance()
    let meaunUrls = ref([])
    let cookie = ref("");
    let hotSongCat = ref([])
    let coverImgUrl = ref([])
    let content = ref([])
    let songListId = ref('')
    let authorName = ref([])
    const  router = useRouter()
    async function getHotSong() {
      let {data: res} = await proxy.$axios.get('http://localhost:8081/playlist/hot')
      hotSongCat.value = res.tags;
      let login_url = "http://localhost:8081/login/cellphone?phone=18957138740&md5_password=f0ac80b1743738fe4d3fc421aa6db758"
      let {data: rs} = await proxy.$axios.get(login_url)
      cookie = rs.cookie
    }
    onBeforeMount(async () => {
      await getHotSong()
      hotSongCat.value.forEach((item) => {
        meaunUrls.value.push(`http://localhost:8081/top/playlist/highquality?cat=${encodeURI(item.playlistTag['name'])}`)
      })
      let songInfo = []
      meaunUrls.value.forEach((item) => {
        songInfo.push(new Promise((resolve, rejected) => {
          proxy.$axios.get(item).then((data) => {
            // if(data.data.playlists !== [])
            resolve(data.data)
          }).catch((error) => {
            rejected(error)
          })
        }))
      })
      songInfo = await Promise.all(songInfo);
      let All = []   //歌单详情
      for (let i = 0; i < songInfo.length; i++) {
        if (songInfo[0].playlists)
          All.push(songInfo[0].playlists)
      }
      All = All[2]
      let ids = []  //歌单id
      All.forEach((item) => {
        ids.push(item.id)
        coverImgUrl.value.push([item.coverImgUrl,item.name])
      })
      coverImgUrl.value = coverImgUrl.value.splice(0,15)

      ids = ids.splice(0,10)
      let songContent = [] //十个歌单里的所有歌曲(包括name id)
      for(let i of ids){
        let url = `http://localhost:8081/playlist/track/all?id=${i}&limit=10&cookie=${cookie}`
        let {data:res} = await proxy.$axios.get(url)
        songContent.push(res.songs)
      }
      // let content = []

      songContent.forEach((item)=>{
        let oneTip = []
        item.forEach((it,inx)=>{
          oneTip[inx] = [it.name,it.id,it.al.picUrl]
          authorName.value.push(it.ar[0]['name'])
        })
        content.value.push(oneTip)
      })
      for(let i = 0 ;i <content.value.length;i++){
        for(let j = 0; j <content.value[i].length;j++){
          let {data:result} = await proxy.$axios.get(`http://localhost:8081/song/url?id=${content.value[i][j][1]}`)
          content.value[i][j] = [content.value[i][j][0],result.data[0]['url'],content.value[i][j][2]]
        }
      }
    })
    return {
      cookie,
      hotSongCat,
      coverImgUrl,
      content,
      songListId,
      authorName
    }
  }
})
</script>

<style scoped>
.home {
  width: 70%;
  /*height: 50%;*/
  position: absolute;
  left: 22%;
  top: 22%;
  box-sizing: border-box;
}
.home a {
  display: flex;
  flex-direction: column;
  width: 15%;
  height: 15%;
  float: left;
  margin-right: 60px;
  margin-top:5%;
}
.home a{
  text-decoration: none;
  color: black;
}
img{
  border-radius: 1em;
}
</style>
