<template>
  <el-carousel :interval="4000" type="card" height="200px">
    <el-carousel-item v-for="url in urls" :key="url.targetId">
      <img :src="url.imageUrl" alt="">
    </el-carousel-item>
  </el-carousel>
</template>


<script>
import {computed, defineComponent, getCurrentInstance, onBeforeMount, ref} from "vue";

export default defineComponent({
  name: "lunbo",
  setup(props, ctx) {
    let urls = ref([])
    const { proxy } = getCurrentInstance()
    onBeforeMount(async ()=>{
      const {data:res} = await proxy.$axios.get('http://localhost:8081/banner?type=0');
      urls.value = res.banners
    })
    return {urls}
  }
})
</script>

<style scoped>
.el-carousel__item{
  /*width:30%*/
}
.el-carousel__item img{
  width:100%;
  height:100%
}
.el-carousel{
  position: absolute;
  width:50%;
  left:50%;
  top:0;
  transform: translateX(-35%);
}
img{
  border-radius: 1em;
}
</style>
