import Home from '../components/Meaun/Home'
import {  createRouter,createWebHashHistory } from 'vue-router'
import subHome from "../components/subHome";
const history = createWebHashHistory()
const rts = createRouter({
    history,
    routes:[
        { path: "/", redirect: "/home" },
        {
            path: "/home",
            name: "home",
            component: Home,
        },
        {
            path:'/subHome',
            name:'subHome',
            component:subHome
        }
    ],
})

export default rts
